:root {
  /* Cores para o tema claro */
  --loader-fundo: #C7B0E1;
  --loader-carregamento: #7c4c86;

  --welcome-fundo: #9d85bb;
  --welcome-fonte: #f4f4f5;
  --welcome-destaque-fonte: linear-gradient(120deg, #4805a0, #a213e4);
  --welcome-button:#8947ec;
  --welcome-button-hover: linear-gradient(90deg, #7f59b4, #9711d4);

  --fundo: #ddc0ff;
  --background-color: #C7B0E1;
  --nav-menu: #D9D9D9;
  --nav-menu2: aliceblue;
  --nav-destaque: black;
  --nav-barra: #1E0044;

  --cor-fonte: black;

  --botao-cor1: #874CDD;
  --botao-cor2: #1E0044;
  --botao-sol: #d8860b;
  --botao-lua: #C7B0E1;

  --circulo1: #8d75b3;
  --circulo2: #77448b;
  --circulo3: #84598d;

  --presentation: #947BB2;
  --detalhe: #7e57aa;
  --fonte-presentation: white;
  --destaque-fonte-presentation: linear-gradient(120deg, #1E0044, #B923FF);
  --highlight-color: #5c1ab3;

  --tech: #684B93;
  --fonte-tech: white;
  --titulo-tech: #492875;
  --h2-tech: rgba(252, 252, 252, 0.6);
  --btn-outro: #692BC5;
  --btn-back: #874CDD;
  --btn-front: #A179DC;
  --btn-reset: rgb(207, 35, 35);
  --container-icon: linear-gradient(90deg, #684B93, #B07FF9);
  --container-nivel: #7335cf;
  --border-nivel: #492875;
  --button-hover-color: #2E0E5B;

  --font-project-categoria: black;
  --fundo-project: #E9DEFF;
  --sombra-project: 0px 10px 20px rgba(0, 0, 0, 0.2);
  --button-project-tech: #492875;
  --button-fonte-project-tech: white;
  --font-project-descricao: black;
  --button-link-project: #8338EC;
  --button-link-project-hover: linear-gradient(90deg, #8338EC, #B923FF);
  --button-link-project-fonte: white;
  --image-project: #492875;
  --btn-paginacao-project: #6e00ff;
  --btn-paginacao-project-fonte: white;
  --btn-paginacao-project-hover: linear-gradient(90deg, #6e00ff, #B923FF);
  --btn-paginacao-project-disabled: #afabab;
  --btn-paginacao-project-fonte2: white;
  --wrapper-project: #874CDD;
  --wrapper-project-fonte: rgb(52, 12, 90);

  --footer-fundo: #b58dbe;
  --footer-fonte: black;
  --footer-hr: #7c4c86;
}

.dark-theme {
  /* Cores para o tema escuro */
  --loader-fundo: #492875;
  --loader-carregamento: #7c4c86;

  --fundo: #492875;

  --background-color: #15022E;
  --nav-menu: #692BC5;
  --nav-menu2: black;
  --nav-destaque: #C7B0E1;
  --nav-barra: #d8860b;

  --cor-fonte: #C7B0E1;

  --botao-cor1: #3d0a71;
  --botao-cor2: #b17ef7;

  --presentation: #2E0E5B;
  --detalhe: #614b81;
  --fonte-presentation: white;
  --destaque-fonte-presentation: linear-gradient(120deg, #530bb1, #cf0fff);
  --highlight-color: #8747e6;

  --tech: #763acf;
  --titulo-tech: #B07FF9;
  --h2-tech: rgba(252, 252, 252, 0.6);
  --btn-outro: #32145F;
  --btn-back: #4F07BA;
  --btn-front: #572C98;
  --container-icon: linear-gradient(90deg, #4F07BA, #572C98);
  --container-nivel: #400292;
  --border-nivel: #874CDD;
  --button-hover-color: #874CDD;

  --font-project-categoria: #E9DEFF;
  --fundo-project: #330775;
  --sombra-project: 0px 10px 20px rgba(148, 148, 148, 0.151);
  --button-project-tech: #d76ef1;
  --button-fonte-project-tech: #E9DEFF;
  --font-project-descricao: #E9DEFF;
  --button-link-project: #bc2cf5;
  --button-link-project-hover: linear-gradient(90deg, #8338EC, #cf0fff);
  --button-link-project-fonte: white;
  --image-project: #8338EC;
  --btn-paginacao-project: #5707be;
  --btn-paginacao-project-fonte: white;
  --btn-paginacao-project-hover: linear-gradient(90deg, #8917e7, #2c0e5b);
  --btn-paginacao-project-disabled: #afabab;
  --btn-paginacao-project-fonte2: white;
  --wrapper-project: #6738ad;
  --wrapper-project-fonte: rgba(215, 197, 231, 0.932);

  --footer-fundo: #705199;
  --footer-fonte: white;
  --footer-hr: rgb(134, 98, 141);
}

@import url('https://fonts.googleapis.com/css2?family=Asul:wght@400;700&family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200..900;1,200..900&display=swap');


body {
  margin: 0;
  font-family: 'Asul', 'Montserrat', 'Source Code Pro', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--fundo);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}